@font-face {
  font-family: 'Ubuntu Mono';
  src: url("/fonts/ubuntu-mono/ubuntu-mono-regular.ttf");
}
@font-face {
  font-family: 'Open Sans';
  src: url("/fonts/open-sans/open-sans-light.ttf");
}
@font-face {
  font-family: Slabo;
  src: url("/fonts/slabo-27px/slabo-27px-regular.ttf");
}
body {
  margin: 0;
}
a {
  font-family: 'Open Sans', sans-serif;
  color: #32373c;
}
#menu-outer {
  font-family: 'Open Sans', sans-serif;
  padding: 0.6rem 1rem;
  border-bottom: 1px solid #c8d2dc;
  background-color: #f0f5fa;
}
#menu-outer #menu-inner a,
#menu-outer #menu-inner div {
  display: inline-block;
  margin-right: 0.6rem;
}
#about-outer {
  padding: 0.6rem 1rem;
  display: flex;
}
#about-outer #about-left {
  padding: 0.6rem 1rem;
  font-family: 'Slabo', serif;
  flex: 0.6;
}
#about-outer #about-right {
  flex: 0.4;
}
#about-outer #about-right img {
  width: 200px;
}
#content-outer {
  padding: 0.6rem 1rem;
  width: 960px;
  margin: auto;
}
#content-outer p,
#content-outer span,
#content-outer b,
#content-outer em,
#content-outer i,
#content-outer strong,
#content-outer a,
#content-outer time,
#content-outer div {
  font-family: 'Slabo', serif;
}
#content-outer h1,
#content-outer h2,
#content-outer h3,
#content-outer h4,
#content-outer h5,
#content-outer h6 {
  font-family: 'Open Sans', sans-serif;
  color: #1e2328;
}
#content-outer h1>*,
#content-outer h2>*,
#content-outer h3>*,
#content-outer h4>*,
#content-outer h5>*,
#content-outer h6>* {
  font-family: 'Open Sans', sans-serif;
}
#content-outer code,
#content-outer pre {
  font-family: 'Ubuntu Mono', Inconsolata, Menlo, Consolas, monospace;
  background-color: #f0f5fa;
}
#content-outer blockquote {
  padding: 0.6rem 1rem;
  background-color: #f0f5fa;
}
#content-outer .examples {
  display: flex;
  flex-wrap: wrap;
}
#content-outer .example {
  padding: 0.6rem 1rem;
  flex: 0.3;
  border: 1px solid #c8d2dc;
  background-color: #f0f5fa;
  text-align: center;
  margin-right: 0.6rem;
  flex-direction: column;
}
#content-outer .example a {
  flex: 0.7;
}
#content-outer .example .caption {
  flex: 0.3;
}
#content-outer .example video,
#content-outer .example img {
  max-width: 200px;
}
#bottom-outer {
  font-family: 'Open Sans', sans-serif;
  padding: 0.6rem 1rem;
  border-top: 1px solid #c8d2dc;
}
#bottom-outer a {
  margin-right: 0.6rem;
}
#github-repos .github-repo-item,
#github-repo-hexo .github-repo-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
  margin-bottom: 0.6rem;
}
#github-repos .github-repo-item .repo-title,
#github-repo-hexo .github-repo-item .repo-title {
  display: flex;
  flex-direction: column;
  flex: 0.7;
}
#github-repos .github-repo-item .repo-title .name,
#github-repo-hexo .github-repo-item .repo-title .name {
  flex: 0.5;
}
#github-repos .github-repo-item .repo-title .description,
#github-repo-hexo .github-repo-item .repo-title .description {
  flex: 0.5;
}
#github-repos .github-repo-item .tech-metrics,
#github-repo-hexo .github-repo-item .tech-metrics {
  display: flex;
  text-align: right;
  margin-left: auto;
}
#github-repos .github-repo-item .tech-metrics .tech-metric,
#github-repo-hexo .github-repo-item .tech-metrics .tech-metric {
  font-family: 'Ubuntu Mono', Inconsolata, Menlo, Consolas, monospace;
  padding: 0.2rem 0.3rem;
  display: flex;
  background-color: #f0f5fa;
  border-radius: 0.6rem;
  font-size: 0.85rem;
  margin-right: 0.6rem;
  height: 1.2rem;
}
#github-repos .github-repo-item .tech-metrics .tech-metric .tech-text,
#github-repo-hexo .github-repo-item .tech-metrics .tech-metric .tech-text {
  color: $color-fg-highlight;
  flex: 0.5;
}
#github-repos .github-repo-item .tech-metrics .tech-metric .tech-bg,
#github-repo-hexo .github-repo-item .tech-metrics .tech-metric .tech-bg {
  flex: 0.5;
}
#github-gists .github-gist-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
}
#github-gists .github-gist-item .gist-description {
  flex: 0.7;
}
#github-gists .github-gist-item .gist-date {
  margin-left: auto;
}
#recent-posts .recent-post-item {
  padding: 0.6rem 1rem;
  display: flex;
  flex-direction: row;
}
#recent-posts .recent-post-item .recent-post-title {
  flex: 0.7;
}
#recent-posts .recent-post-item .recent-post-date {
  margin-left: auto;
}
